* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.messages-container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}

.messages-container h2 {
  -ms-flex-item-align: start;
      align-self: flex-start;
  margin-bottom: 0.5rem;
  padding: 2rem 2rem 0 2rem;
}

.messages {
  list-style: none;
  width: 100%;
  color: white;
  font-family: Karla, sans-serif;
  padding: 0 1rem;
}

.messages h3 {
  width: -moz-fit-content;
  width: -webkit-fit-content;
  width: fit-content;
}

.messagebubble {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  background-color: #212121;
  width: 100%;
  border-radius: 15px;
  padding: 1rem;
  -webkit-transition: all 0.7s;
  -o-transition: all 0.7s;
  transition: all 0.7s;
  overflow: hidden;
  margin-top: 1rem;
  border-right: 8px solid rgb(62, 28, 255);
}

.messagebubble:last-of-type {
  border-right: none;
}

.messagebubble img {
  width: 50px;
  height: 50px;
  -o-object-fit: cover;
     object-fit: cover;
  border-radius: 50%;
}

.nameMessage {
  margin-left: 1rem;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
}

.nameMessage p {
  width: 90%;
  display: block;
  font-weight: 200;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
}