* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.card {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  color: white;
  font-family: Karla, sans-serif;
  font-size: 24px;
  padding: 0 0 2rem 2rem;
  width: 300px;
  height: 300px;
  border-radius: 25px;
  -webkit-transition: all 0.6s ease-out;
  -o-transition: all 0.6s ease-out;
  transition: all 0.6s ease-out;
  position: relative;
  overflow: hidden;
}

.slideOutAnimation {
  -webkit-animation: slideOut 3s;
          animation: slideOut 3s;
}

.slideInAnimation {
  animation: slideOut 3s reverse;
}

.card:hover {
  scale: 1.1;
  cursor: pointer;
}

.card:hover .whitebox,
.card:hover .whitebox-behind {
  translate: 300px;
  rotate: 90deg;
}

.whitebox {
  position: absolute;
  border-radius: 25px;
  height: 250px;
  width: 250px;
  rotate: 45deg;
  right: -100px;
  top: -20%;
  background-color: rgba(255, 255, 255, 0.3);
  -webkit-transition: all 1s;
  -o-transition: all 1s;
  transition: all 1s;
}

.whitebox-behind {
  position: absolute;
  border-radius: 25px;
  height: 250px;
  width: 250px;
  rotate: 45deg;
  right: -100px;
  top: -5%;
  background-color: rgba(255, 255, 255, 0.3);
  -webkit-transition: all 1s;
  -o-transition: all 1s;
  transition: all 1s;
}

.orange {
  background: -webkit-gradient(linear, left top, left bottom, from(#FF9344), to(#FF7B43));
  background: -o-linear-gradient(top, #FF9344 0%, #FF7B43 100%);
  background: linear-gradient(180deg, #FF9344 0%, #FF7B43 100%);
}

.purple {
  background: -webkit-gradient(linear, left top, left bottom, from(#CE44FF), to(#FF43D6));
  background: -o-linear-gradient(top, #CE44FF 0%, #FF43D6 100%);
  background: linear-gradient(180deg, #CE44FF 0%, #FF43D6 100%);
}

.blue {
  background: -webkit-gradient(linear, left top, left bottom, from(#5E44FF), to(#43C7FF));
  background: -o-linear-gradient(top, #5E44FF 0%, #43C7FF 100%);
  background: linear-gradient(180deg, #5E44FF 0%, #43C7FF 100%);
}

@-webkit-keyframes slideOut {
  from {
    right: -100px;
    rotate: 45deg;
  }
  to {
    right: -300px;
    rotate: 90deg;
  }
}

@keyframes slideOut {
  from {
    right: -100px;
    rotate: 45deg;
  }
  to {
    right: -300px;
    rotate: 90deg;
  }
}